/* ==========================================================================
# Simple Blog List style - begin
========================================================================== */
@include ipad-landscape {
	.qode-simple-blog-list {

		&.qode-sbl-4-columns {
			.qode-sbl-post {

				width: 50%;

				&:nth-child(2n+1){
					clear: both;
				}

				&:nth-child(3n+1),
				&:nth-child(4n+1){
					clear: none;
				}
			}
		}

		&.qode-sbl-5-columns {
			.qode-sbl-post {
				width: 50%;

				&:nth-child(5n+1){
					clear: none;
				}
				&:nth-child(2n+1){
					clear: both;
				}

			}
		}
	}
}
@include ipad-landscape {
	.qode-simple-blog-list {

		.qode-sbl-post {

			width: 100%;

		}
		&.qode-sbl-4-columns {
			.qode-sbl-post {
				width: 100%;
			}
		}

		&.qode-sbl-5-columns {
			.qode-sbl-post {
				width: 100%;
			}
		}
	}
}
/* ==========================================================================
# Simple Blog List style- end
========================================================================== */