/* ==========================================================================
# Image With Overlapping Info style - begin
========================================================================== */

@include ipad-landscape {
	.qode-image-with-overlapping-info {

		.qode-iwoi-image-holder {
			padding-right: 0;
		}

		.qode-iwoi-info-holder {
			width: 480px;
			padding: 48px 50px;

			.qode-iwoi-link {
				margin-top: 50px;
			}

		}

	}
}

@include phone-landscape {
	.qode-image-with-overlapping-info {

		.qode-iwoi-info-holder {
			left: 0;
			margin-top: -5%;
			width: 100%;
			padding: 48px 50px;

			.qode-iwoi-link {
				margin-top: 40px;
			}

		}

	}
}
/* ==========================================================================
# Image With Overlapping Info style - end
========================================================================== */